import React from 'react';
import php from '../../assets/img/php-1.png'
import wp from '../../assets/img/wp.png'
import Schema from '../../layout/schemas/ProgramsSingleSchema';
import Page from '../../layout/Page';

class Start extends React.Component {
  state = {
    show: false
  };

  componentDidMount() {
    setTimeout(() => {
      this.setState({show: true})
    }, 1000)
  }

  render() {

    return (
        <Page id="php" title="[PHP]" description="PHP, WordPress kursas" className="Program PHP">
          {this.state.show && <Schema/>}
          <div className="container">
            <h1>Internetini svetaini ir  <span>parduotuvi kursas</span></h1>
            <br/>
            <h2>Apie program</h2>
            <p>
              is kursas specializuotas  PHP programavimo kalb, MySQL duombaz ir WordPress sistemos integravim. Pabaigus j js galsite konstruoti "web" aplikacijas, turinio valdymo sistemas ar WordPress temas.
            </p>
            <h2>
              PHP
              <img src={php} className="logo-one" alt="php"/>
            </h2>
            <p>
              Tai viena i labiausiai paplitusi programavimo kalb. Tai serverin (back-end) programavimo kalba, kuri dl savo paprastumo leidia greitai sukurti dinaminius internetinius puslapius, apdoroti duomenis, prijungti duombazes ir kurti sistemas.
            </p>
            <h2>
              WordPress
              <img src={wp} className="logo-two" alt="wordpress"/>
            </h2>
            <p>
              WordPress - tai turinio valdymo sistema paremta PHP kalba ir MySQL duombaze. Daugiau nei 30% internetini svetaini veikia ios sistemos pagalba. Naudojantis WordPress sistema, galima greitai ir paprastai sukurti internetines svetaines, "blogus" ar internetines parduotuves.
            </p>
            <h2>Kursas</h2>
            <p>
              Kurso trukm 12 savaii (72val), po tris usimimus per savait po dvi valandas.
              <br/>
              Paskaitos vyksta kas antr dien, todl bus pakankamai laiko pasidaryti praktinius darbus ir imokti praeit mediag.
              <br/>
              Kurso metu pirmasis mnuo yra skirtas PHP pagrindam ir MySQL duombazs studijoms. Per  mnes susipainsime su serverins dalies programavimu (Back-end), imoksime PHP kalbos sintaks, veikimo principus, MySQL duobazs integravim. Apjungus ias technologijos sukonstruosime darbuotoj valdymo aplikacij, sukelsime j  server. Likusius du mnesius gilinsims  WordPress tem krim, kur nuo pagrind sigilinsim  ios sistemos subtilybes. WordPress yra ypa plaiai paplits ir daug moni nuolat ieko ios technologijos specialist. Pabaigus  kurs tursite pakankamai praktikos tiek darbui monje tiek darbui savarankikai.
              <br/>
              <b>Kurso eiga:</b>

            </p>
            <ul>
              <li>PHP kalbos pagrindai</li>
              <li>MySQL duombazs integravimas ir naudojimas</li>
              <li>Aplikacijos krimas apjungiant abi technologijas</li>
              <li>WordPress pagrindai</li>
              <li>WordPress tem krimas</li>
              <li>Daugiau nei 5 praktiniai nam darbai ir baigiamasis kursinis projektas</li>
              <li>Konsultacijos ir ikilusi problem aptarimas</li>
              <li>Pasiruoimas darbo pokalbiui</li>
            </ul>
            <h3 className="offset">
              Trukm: 12 savaii - 72val
            </h3>
            <h3><span className="underline">Kaina: 195/mn</span></h3>
            <p className="offset">
              {/*<i>Artimiausias kursas: <b>2018-09-24</b></i>*/}
              <br/>
              Registracija:
              <br/>
              <b className=""><a href="tel:+370 647 01234">+370 647 01234</a></b>
              <br/>
              <b><a href="mailto:info@ready2code.lt">info@ready2code.lt</a></b>
            </p>
          </div>
        </Page>
    );
  }
}
export default Start


