import React from 'react';
import Schema from '../../layout/schemas/ProgramsSingleSchema';
import Page from '../../layout/Page';

class Start extends React.Component {
  state = {
    show: false
  };

  componentDidMount() {
    setTimeout(() => {
      this.setState({show: true})
    }, 1000)
  }

  render() {

    return (
        <Page id="idarbinimas" title="[darbinimas ir praktika]" description="darbinimas ir praktika po studij Lietuvoje ir usienyje" className="Program Work">
          {this.state.show && <Schema/>}
          <div className="container">
            <h1>Praktika<span>darbinimas</span></h1>
            <br/>
            <p>
                <b>[ ready2<strong>code</strong> ]</b> aktyviai bendradarbiauja su <a href="https://atranka360.lt/" rel="noopener noreferrer"  target="_blank"><span className="underline">Atranka 360</span></a> darbuotoj atrankos kompanija. Studentams baigusius React.js, Node.js ar PHP program ir atsiskaiius visus praktinius darbus padedame sidarbinti ar pradti savo individuali veikl.
            </p>

          </div>
        </Page>
    );
  }
}
export default Start


