import React from 'react';
import reactjs from '../../assets/img/react.png';
import Schema from '../../layout/schemas/ProgramsSingleSchema';
import Page from '../../layout/Page';

class Start extends React.Component {
  state = {
    show: false
  };

  componentDidMount() {
    setTimeout(() => {
      this.setState({show: true})
    }, 1000)
  }

  render() {

    return (
        <Page id="react" title="[react.js]" description="React.js kursas" className="Program React">
          {this.state.show && <Schema/>}
          <div className="container">
            <h1 className="react">Web ir mobilij <span>aplikacij kursas</span></h1>
            <br/>
            <h2>Apie program</h2>
            <p>
              is kursas yra skirtas web ir mobilij aplikacij krimui. Tai kursas paengusiems studentams ar programuotojams, turintiems HTML, CSS, JavaScript kalb pagrindus.
              <br/>
              Paskait metu js praplsite jau turimas Front-end inias, imoksite kurti "web" ir mobilisias aplikacijas naudojant JavaScript kalbos bibliotek React.js.
            </p>
            <h2>
              React.js
              <img src={reactjs} alt="react" className="logo react"/>
            </h2>
            <p>
              JavaScript pagrindinis web aplikacij variklis, kadangi tai vienintel programavimo kalba kuri veikia moderniose naryklse. Jos pagalba galima sukurti spariai veikianias aplikacijas ir puslapius.
              <br/>
              Naudojantis tik JavaScript, greitai patampa keblu konstruoti didesnes aplikacijas, ieitis - React.js.
              <br/>
              React.js tai JavaScript kalbos biblioteka, kuri 2013 metais buvo sukurta Facebook komandos. iuo metu ji yra viena i populiariausi bibliotek konstruojant interaktyvias internetines svetaines ir aplikacijas. i technologij naudoja tokios kompanijos kaip Facebook, Netflix, Airbnb, Instagram ir kitos.
              <br/>
              React.js pagalba aplikacijos yra konstruojamos komponent pagalba. Juos galime sukurti ir daug kart panaudoti neperraant kodo kelis kartus, atvaizduoti dinamikai pagal slygas. Kadangi u komponent atvaizdavim yra atsakinga JavaScript kalba, aplikacijose nebna uvlavim ar puslapio perkrovim, o tai suteikia labai skland narym sukurtoje sistemoje.
            </p>
            <h2>Kursas</h2>
            <p>
              Kurso trukm 12 savaii (72val), po tris usimimus per savait po dvi valandas.
              <br/>
              Paskaitos vyksta kas antr dien, todl bus pakankamai laiko pasidaryti praktinius darbus ir imokti praeit mediag.
              <br/>
              Kursas prasideda nuo pai pagrind, isiaikinant kam ir kodl mums reikia tokio rankio. Eigoje sukonstruosime dvi pilnai veikianias aplikacijas, sigilinsime  bibliotekos subtilybes ir orientuosims kaip visk apjungti su serverine dalimi panaudojant API uklausas.
              <br/>
              <b>Kurso eiga:</b>

            </p>
            <ul>
              <li>Isami ES6 JavaScript sintaks</li>
              <li>React.js pagrindai ir subtilybs, npm pakuoi sistema</li>
              <li>API uklausos ir autentikacija</li>
              <li>Net dvi pilnai ibaigtos ir veikianios aplikacijos</li>
              <li>Aplikacij diegimas (deployment)</li>
              <li>Daugiau nei 6 praktiniai  darbai ir baigiamasis kursinis projektas</li>
              <li>Konsultacijos ir ikilusi problem aptarimas</li>
              <li>Pasiruoimas darbo pokalbiui</li>
            </ul>

            <h3 className="offset">
              Trukm: 12 savaii - 72val
            </h3>
            <h3><span className="underline">Kaina: 195/mn</span></h3>
            <p className="offset">
              {/*<i>Artimiausias kursas: <b>2018-09-24</b> <br/></i>*/}
              Registracija:
              <br/>
              <b className=""><a href="tel:+370 647 01234">+370 647 01234</a></b>
              <br/>
              <b><a href="mailto:info@ready2code.lt">info@ready2code.lt</a></b>
            </p>
          </div>
        </Page>
    );
  }
}
export default Start


