import React from 'react';
import Schema from '../../layout/schemas/AboutInnerSchema';
import coach1 from '../../assets/img/k1.jpg';
import Page from '../../layout/Page';

class Teachers extends React.Component {
  state = {
    show: false
  };

  componentDidMount() {
    setTimeout(() => {
      this.setState({show: true})
    }, 1000)
  }

  render() {
    return (
        <Page id="destytojai" className="About-inner-page" title="[dstytojai]" description="Akademijos destytojai">
          {this.state.show && <Schema/>}
          <div className="container">
            <h1 className="glitch-once" data-text="Dstytojai">
              Dstytojai
              <span/>
            </h1>
            <div className="coach">
              <h2>Karolis Kerpys</h2>
              <img src={coach1} alt="karolis kerpys"/>
            </div>
            <p>
              Mokyklos krjas, dstytojas, programuotojas.
              <br/>
              Programavimo patirtis - 5 metai.
              <br/>
              Dstytojavimo patirtis - 2 metai, paruota vir 150 programuotoj.
              <br/>
              Programavimo kalbos ir rankiai:
            </p>
            <ul>
              <li>HTML, CSS, JavaScript</li>
              <li>PHP</li>
              <li>React.js</li>
              <li>Node.js</li>
              <li>Python</li>
              <li>Photoshop, Illustrator</li>
            </ul>
            <p>
              Programavimas ir technologijos mane jau traukia nuo vaikysts. Dar bdamas vaikas labai mgau LEGO
              konstruktorius, elektronik, iradimus. Pabaigiau Vilniaus Gedimino technikos universitet - statybos
              ininerij. Po keli met darbo supratau jog tai ne mano sritis ir pradjau mokintis programavimo kalb.
            </p>
            <p>
              Studijuoti programavimo kalbas savarankikai, tikrai sunkus ir ilgas procesas. Kelias buvo banguotas -
              neinai koki kalb pasirinkti,  koki srit specializuotis, kaip pirmus usakovus susirasti. Bet i sritis trauk mane ypa stipriai ir ilgai netruko kai jau pirmus usakymus gavau. iuo metu specializuojuosi  moni valdymo sistem, mobilij ir "web" aplikacij krim. Teko birbti su tokiomis monmis kaip Bobcat, Kretingos grdai, ten diegti klient ir gamybos valdymo sistemas.
            </p>
            <p>
              Laikui bgant atsirado galimyb savo iniomis pasidalinti ir su kitais. Tad paskutinius por met dsiau
              programavimo pagrind ir subtilybi kitose mokyklose. Taiau isiskyr ms vizija ir tikslai.
            </p>
            <p>
              Dirbti su mogumi ir perteikti jam savo inias yra atsakingas ir svarbus darbas. Svajojau vesti paskaitas kuriose yra maos moni grups, kruopiai apgalvota programa kur studentas gali tikslingai judti savo tikslo link. Norjosi sukurti programuotoj bendruomen, kurioje bt visi studijuojantys ir jau dirbantys, besidalijantys reikalinga informacija ir pagalba.
            </p>
            <p>
              Tad paskatintas savo student ir draug nusprendiau gyvendinti i svajon. Rugsjo 1 dien atidaryta programavimo mokykla <b className="ready2code">[ready2<strong>code</strong>]</b>
            </p>

          </div>

        </Page>
    );
  }
}
export default Teachers


