import React from 'react';
import html from '../../assets/img/HTML_Logo.png'
import css from '../../assets/img/csslogo.png'
import js from '../../assets/img/Javascript-shield.png'
import Schema from '../../layout/schemas/ProgramsSingleSchema';
import Page from '../../layout/Page';
import Popup from '../../components/Popup';
import Aux from '../../components/Aux';

class Start extends React.Component {
  state = {
    showPopup: false,
    showSchema: false,
    seen: false,
    sent: false,
    type: 'start'
  };

  componentDidMount() {
    const submitTime = localStorage.getItem(this.state.type);
    console.log(Date.now(), submitTime);
    if (submitTime && Date.now() > (parseInt(submitTime) + 1000 * 10)) {
      // this.setState({send:true, seen:true})
    }
    document.addEventListener('keydown', this.hidePopup);
    this.setState({showSchema: true});
    setTimeout(() => {
      if (!this.state.seen) this.setState({showPopup: true})
    }, 3000)
  }

  componentWillUnmount() {
    document.removeEventListener('keydown', this.hidePopup)
  }

  hidePopup = (e) => {
    if (e === 'success') {
      this.setState({showPopup: false, seen: true, sent: true})
    } else if (e.keyCode === 27 || e.type === 'click' || e === 'success') {
      this.setState({showPopup: false, seen: true})
    }
  };
  showPopup = () => {
    this.setState({showPopup: true, seen: true})
  };

  render() {

    return (
        <Aux>
          {/*<Popup*/}
              {/*heading="Artimiausias kursas spalio 8d!"*/}
              {/*text="Registruokis ar gauk daugiau informacijos"*/}
              {/*type={this.state.type}*/}
              {/*show={this.state.showPopup}*/}
              {/*hidePopup={this.hidePopup}*/}
          {/*/>*/}
          <Page id="pagrindai" title="[programavimo pagrindai]"
                description="Programavimo pagrindai: HTML, CSS, JavaScript" className="Program Start">
            {this.state.showSchema && <Schema/>}
            <div className="container">
              <h1>Programavimo <span>pagrindai</span></h1>
              <br/>
              <h2>Apie program</h2>
              <p>
                is kursas yra skirtas norintiems imokti kurti internetines svetaines, susipainti su puslapi
                architektra ir animaciniais efektais, gyti programavimo JavaScript kalba pagrindus. Jis puikiai
                tinkamas
                pradedantiesiems ar jau susipainusiems su programavimu, web dizaineriams, ar tiems kurie nori pamginti
                savo jgas naujoje sferoje.
                <br/>

                <br/>
                <b>
                  <span className="">Kalbos:</span> HTML, CSS, JavaScript
                </b>
              </p>
              <h2>
                HTML
                <img src={html} className="logo-one" alt="html"/>
              </h2>
              <p>
                HTML yra pagrindin kalba naudojama kuriant internetini puslapi struktras.
                Tai tarsi namo statybiniai blokai kuri pagalba puslapyje sudedami esantys elementai. i kalba yra
                pakankamai lengva, todl j sisavinti pakaks ir keli dien darbo, o rezultatai matomi ikart.
              </p>
              <h2>
                CSS
                <img src={css} className="logo-two" alt="css"/>
              </h2>
              <p>
                i kalba yra palaikoma vis internetini narykli ir yra skirta stilizuoti HTML elementams esantiems
                puslapiuose. CSS kalb lengva pradti naudoti ir ji js svetainms suteiks daug iraiking atspalvi
                bei
                efekt.
              </p>
              <h2>
                JavaScript
                <img src={js} className="logo-three" alt="js"/>
              </h2>
              <p>
                Tai vienintel programavimo kalba kuri veikia moderniose naryklse. Ji leidia apjungti puslapio
                statinius elementus ir padaryti juos interaktyvius. Imok j tursite gerus programavimo pagrindus -
                susipainsite su funkcijomis, kintamaisiais, objektais. Galsite lengvai kurti interaktyvius puslapius,
                efektus, aplikacijas ar net aidimus.
              </p>
              <h2>Kursas</h2>
              <p>
                Kurso trukm - 12 savaii (72val), po tris usimimus per savait po dvi valandas. Paskaitos vyksta kas
                antr dien, todl bus pakankamai laiko pasidaryti praktinius darbus ir imokti praeit mediag.
                Kursas yra sudarytas i trij dali:
              </p>
              <p>
                <b>Pirmas mnuo</b> - internetini svetaini architektra ir stilistika. HTML+CSS.
              </p>
              <ul>
                <li>Susipainimas su programavimu, naryklmis, serveriais ir kodo redaktoriumi</li>
                <li>HTML ir CSS pagrindai</li>
                <li>Puslapi architektros pagrindai</li>
                <li>Puslapio element stilizavimas</li>
                <li>Pritaikymas mobiliesiems renginiams</li>
                <li>Praktini projekt konstravimas</li>
                <li>Internetinio adreso registravimas, projekto talpinimas  server</li>
              </ul>
              <p>
                <b>Antras mnuo</b> - isamus JavaScript kalbos studijavimas, kurio metu susipainsite su funkcijomis,
                slygomis ir visais kitais programavimo rankiais. Konstruosime internetines aplikacijas, aidimus ir
                interaktyvias internetines svetaines.

              </p>
              <ul>
                <li>Programavimo pagrindai JavaScript kalba</li>
                <li>Kintamieji, slygos, ciklai, funkcijos, objektai ir kita..</li>
                <li>Loginiai programavimo udaviniai</li>
                <li>Interaktyvi puslapi (parallax) efekt konstravimas</li>
                <li>Aplikacij ir aidim krimas</li>
                <li>Duomen uklausos  iorinius serverius (API)</li>
                <li>Kripto valiutos krepelio aplikacijos krimas</li>
              </ul>
              <p>
                <b>Treias mnuo</b> - apjungsime visas kalbas ir kursime baigiamj projekt, pagal dizainerio pateikus
                duomenis, kur galiausiai patalpinsime  server.
              </p>
              <ul>
                <li>Pasiruoimas projektui</li>
                <li>Svetains imatavimai ir sukarpymas</li>
                <li>Interaktyvi komponent konstravimas</li>
                <li>SASS preprocessor integravimas</li>
                <li>PHP ir WordPress pagrindai</li>
                <li>Projekto talpinimas  server</li>
              </ul>
              <p>
                Pabaigus  kurs js tursite gerus programavimo pagrindus, bsite susipain su pagrindiniais
                programavimo terminais, imok kurti internetines svetaines ir aplikacijas.
              </p>
              <h3 className="offset">
                Trukm: 12 savaii - 72val
              </h3>
              <h3><span className="underline">Kaina: 165 /mn</span></h3>
              <p className="offset">
                <i>Artimiausias kursas: <b>2018-10-08</b></i>
                {!this.state.sent &&
                <button
                    onClick={this.showPopup}
                    className="register">
                  Registruotis!
                </button>
                }
              </p>
            </div>
          </Page>
        </Aux>
    );
  }
}
export default Start


