import React from 'react';
import Schema from '../../layout/schemas/AboutInnerSchema';
import img from '../../assets/img/auditorija.jpg';
import Page from '../../layout/Page';

export default class  extends React.Component {
  state = {
    show: false
  };

  componentDidMount() {
    setTimeout(() => {
      this.setState({show: true})
    }, 1000)
  }

  render() {
    return (
        <Page id="studijos" className="About-inner-page" title="[studijos]" description="Programavimo studijos ir kursai">
          {this.state.show && <Schema/>}
          <div className="container">
            <h1 className="glitch-once" data-text="Mokykla">
              Studijos
              <span/>
            </h1>
            <p>
              <b className="ready2code">[ready2<strong>code</strong>] </b> ruoia  programavimo
              specialistus atsivelgdama  naujausias programavimo kalb tendencijas ir specialist poreik. Ms tikslas ne tik paruoti aukto lygio specialistus, bet ir sukurti jauki bendruomen kurioje bt dalinamasi pasiekimais, darbo pasilymais ar ikilusiais ikiais.
            </p>
            <p>
              Mokykla yra sikrusi Uupyje, ia visad jauki aplinka ir bendruomen.
               paskaitas galite atvaiuoti dviraiu ir saugiai j ia palikti ar nemokamai pasistatyti automobil.
            </p>
            <p>
              Paskaitas veda dstytojai-programuotojai, turintys daug patirties ne tik IT
              sferoje, bet ir dirbant su studentais, vedant paskaitas.
              Tik pradedaniojo programuotojo laukia didiulis kiekis informacijos, todl dstytojo tikslas - perteikti
              reikaling informacij, bti js mentoriumi, atsakyti  jums ikilusius klausimus.
            </p>
            <p>
              Kurso metu
              <ul>
                <li>js dirbsite su dstytoju pagal i anksto apraytas programas</li>
                <li>maos 5-8 moni grups</li>
                <li>jums bus pateikiamos reikalingos nuorodos ir dokumentacija</li>
                <li>aikiai ir suprantamai paruotos skaidrs</li>
                <li>nam darbai orientuoti  praeitas temas ir suskirstyti pagal lygius</li>
                <li>visi moduliai turi baigiamj projekt</li>
              </ul>
            </p>
            <p>
              Atvir dur dienos - kasdien. Visada galite apsilankyti ir aptarti ikilusius klausimus ar sudalyvauti paskaitose.
            </p>
            <img src={img} alt="auditorija" className="room"/>
          </div>

        </Page>
    );
  }
}


